@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

body { font-family: Arial, sans-serif; font-size: 12px; line-height: 1.4; }

a { cursor: pointer; text-decoration: none; }

body { font-family: "Lato"; font-size: 14px; }

.container { margin: 0 auto; max-width: 1170px; }

.atom { padding: 10px; margin: 20px; border: 1px solid #ccc; }

.newsletter { margin: 40px auto; position: relative; width: 870px; padding: 50px 60px; background-image: url(/img/bg-newsletter.jpg); color: #fff; }
.newsletter__in { padding: 92px 0 56px 0; text-align: center; }
.newsletter__title { font-size: 48px; }
.newsletter__descr { font-size: 15px; }
.newsletter__descr span { color: #ee4415; }

.input { background: none; border: 1px solid rgba(255, 255, 255, 0.3); font-size: 13px; font-weight: 500; color: #b6b6b6; }
.input__black { width: 100%; padding: 12px 16px; }

.btn { display: inline-block; font-size: 14px; color: #fff; background-color: #DC5736; cursor: pointer; transition: all 0.2s ease-in; }
.btn:hover { background-color: #1e1e1e; }
.btn_std { text-transform: uppercase; border-radius: 30px; padding: 11px 32px; }
.btn_square { padding: 13px 0 12px 0; font-size: 15px; font-weight: 500; width: 100%; }
.btn_square i { margin: 0 4px 0 0; }
.btn_close { position: absolute; top: 0; right: 0; padding-right: 8px; font-size: 25px; font-weight: 600; z-index: 1; }
.btn_close:before { content: ""; position: absolute; top: -39px; right: -36px; background-color: #DC5736; padding: 55px 30px 55px 35px; z-index: -1; transform: rotate(-45deg); }
.btn_find { cursor: pointer; display: inline-block; width: 180px; height: 50px; background-image: url(/img/bg-btn.png); }
.btn_nav { display: none; padding: 9px 14px; border-radius: 2px; cursor: pointer; width: 100%; color: #fff; text-transform: uppercase; font-weight: 600; background-color: #ee4415; }
.btn_nav i { display: inline-block; margin-right: 5px; }
@media only screen and (max-width: 1024px) { .btn_nav { display: block; } }

.subscribe { padding-top: 40px; margin: 0 auto; max-width: 400px; }
.subscribe__action { margin-top: 21px; }

.top-menu { background-color: #272727; *zoom: 1; }
.top-menu:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 768px) { .top-menu { display: table; } }
@media only screen and (max-width: 603px) { .top-menu { text-align: center; } }
.top-menu__left { float: left; }
@media only screen and (max-width: 768px) { .top-menu__left { display: table-cell; } }
@media only screen and (max-width: 603px) { .top-menu__left { width: 100%; } }
.top-menu__right { float: right; }
@media only screen and (max-width: 768px) { .top-menu__right { display: table-cell; } }
@media only screen and (max-width: 603px) { .top-menu__right { width: 100%; } }
.top-menu__list { display: inline-block; }
.top-menu__el { display: inline-block; border-right: 1px solid #333; }
.top-menu__el:first-child { border-left: 1px solid #333; }
@media only screen and (max-width: 768px) { .top-menu__el:first-child { border: none; } }
@media only screen and (max-width: 768px) { .top-menu__el { border: none; } }
.top-menu__link { display: inline-block; padding: 12px 20px 13px 20px; text-transform: uppercase; color: #777; transition: all 0.2s ease-in; }
.top-menu__link:hover { color: #ee4415; }
.top-menu__link:hover .top-menu__count { color: #fff; background-color: #ee4415; border-color: #ee4415; }
.top-menu__count { display: inline-block; padding: 0 6px 1px 6px; margin-left: 9px; border: 1px solid #333; border-radius: 2px; transition: all 0.2s ease-in; }

.top-line { background-color: #272727; }

.header { padding-top: 35px; background-color: #1E1E1E; }
@media only screen and (max-width: 1024px) { .header { padding-top: 0; } }
.header__in { *zoom: 1; }
.header__in:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 1024px) { .header__in { padding: 45px 20px; } }
.header__left { float: left; }
@media only screen and (max-width: 1024px) { .header__left { width: 30%; } }
@media only screen and (max-width: 768px) { .header__left { text-align: center; float: none; width: 100%; } }
.header__right { float: right; }
@media only screen and (max-width: 1024px) { .header__right { width: 70%; } }
@media only screen and (max-width: 768px) { .header__right { padding-top: 25px; float: none; width: 100%; } }

.logo__link { display: inline-block; }

.nav { display: block; background-color: #1E1E1E; *zoom: 1; }
.nav:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 1024px) { .nav { display: none; } }
.nav__list { display: inline-block; float: right; }
.nav__el { display: inline-block; margin-left: 35px; }
.nav__link { display: inline-block; font-size: 16px; color: #fff; padding: 5px 0 37px 0; border-bottom: 3px solid transparent; transition: all 0.2s ease-in; text-transform: uppercase; }
.nav__link:hover { color: #ee4415; border-color: #ee4415; }
.nav__link_active { color: #ee4415; border-color: #ee4415; }

.choose-bike { text-align: center; }

.search { display: inline-block; color: #777; transition: all 0.2s ease-in; }
.search:hover { color: #fff; }
.search__ico { position: relative; top: -2px; left: -2px; }

.promo { position: relative; width: 100%; max-height: 790px; height: 790px; background-image: url(/ninethemes/build/img/goshop-1.png); background-repeat: no-repeat; background-size: cover; }
.promo__img { width: 100%; }
.promo__img_big { width: 550px; height: 270px; }
.promo__big { position: absolute; bottom: 275px; left: 445px; }
.promo__small { position: absolute; bottom: 9px; left: 230px; }

.promo-item { *zoom: 1; }
.promo-item:after { content: " "; display: table; clear: both; }
.promo-item__el { position: relative; float: left; display: block; width: 33.33333%; }
@media only screen and (max-width: 768px) { .promo-item__el { width: 100%; } }
.promo-item__img { display: block; width: 100%; }
.promo-item__descr { color: #fff; position: absolute; width: 220px; top: 107px; right: 32px; }
@media only screen and (max-width: 1270px) { .promo-item__descr { top: 55px; } }
@media only screen and (max-width: 1024px) { .promo-item__descr { right: 0px; top: 35px; } }
@media only screen and (max-width: 768px) { .promo-item__descr { top: 50px; right: 50px; } }
.promo-item__descr_center { right: 82px; }
.promo-item__descr_left { right: 0; left: 45px; }
@media only screen and (max-width: 768px) { .promo-item__descr_left { top: 50px; left: 50px; } }
.promo-item__descr p { font-size: 17px; margin: 4px 0 46px 0; }
@media only screen and (max-width: 850px) { .promo-item__descr p { margin: 4px 0 20px 0; } }
@media only screen and (max-width: 768px) { .promo-item__descr p { margin: 4px 0 46px 0; } }
.promo-item__title { text-transform: uppercase; font-size: 39px; font-weight: 800; }
@media only screen and (max-width: 1024px) { .promo-item__title { font-size: 25px; } }
@media only screen and (max-width: 768px) { .promo-item__title { font-size: 39px; } }
.promo-item__color { margin-left: 7px; font-family: Roboto Slab; font-weight: 300; font-size: 31px; line-height: 54px; vertical-align: top; text-transform: none; }
@media only screen and (max-width: 1024px) { .promo-item__color { font-size: 21px; line-height: 35px; } }
@media only screen and (max-width: 768px) { .promo-item__color { font-size: 31px; line-height: 54px; } }
.promo-item__color_one { color: #eef1c9; }
.promo-item__color_two { color: #f1613d; }
.promo-item__color_three { color: #383f30; }
.promo-item__color_four { color: #525a46; }

.title-section { display: inline-block; margin: 89px auto; background: url(/img/bg-section.png) top center no-repeat; text-align: center; }
.title-section__title { padding-top: 31px; font-size: 34px; font-weight: 600; text-transform: uppercase; }
.title-section__orange { color: #ed5126; }
.title-section p { margin-top: 5px; font-family: Playball; font-size: 22px; color: #929292; }

.filter__el { display: inline-block; }
.filter__el:before { content: "•"; color: #929292; padding: 0 9px; }
.filter__el:first-child:before { content: ""; }
.filter__link { color: #929292; text-transform: uppercase; font-weight: 600; font-size: 15px; }
.filter__link_active { color: #1f1f1f; }


